/* Main container styling */
div {
    font-family: Arial, sans-serif;
    margin: 20px;
  }
  
  /* Input box styling */
  input {
    padding: 10px;
    margin-bottom: 20px;
    font-size: 16px;
    width: 100%;
    box-sizing: border-box;
  }
  
  /* Table styling */
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  /* Table header styling */
  th {
    background-color: #f2f2f2;
    color: black;
    text-align: left;
    padding: 14px;
    cursor: pointer;
  }
  
  /* Table data styling */
  td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  /* Hover effect for table rows */
  tr:hover {
    background-color: #f5f5f5;
  }
  
  .container {
    display: flex;
  }
  
  .filter-block {
    width: 30%;
    padding: 20px;
    border-right: 1px solid #ccc;
  }
  
  .file-list {
    width: 70%;
    padding: 20px;
  }

  .reset-button {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    margin-top: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
  }
  
  .reset-button:hover {
    background-color: #0056b3;
  }
  
  