nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #57a9ec;
    padding: 20px 80px;
    box-shadow: 0 5px 15px rgba(237, 110, 61, 0.2);
}
#navbar{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    list-style-type: none;
    padding: 0 20px;
    position: relative;
}
#navbar li{
    list-style-type: none;
    padding: 0 20px;
    position: relative;
}
#navbar li a{
    text-decoration: none;
    font-size: medium;
    font-weight: 600;
    color: azure;
}
#navbar li a:hover{
    color: #ef5a5a;
}
#navbar li a:hover::after,
#navbar li a.active::after{
    content: "";
    width: 30%;
    height: 2px;
    background: #00b4d8;
    position: absolute;
    bottom: -4px;
    left: 20px;
}