/* Create a search bar style for Imagesearch.js
/* Path: src/components/Imagesearch.css */


.searchButton {
    background: #57a9ec;
    border: 1px solid #57a9ec;
    border-radius: 5px;
    color: azure;
    font-size: 1.2rem;
    padding: 5px 10px;
    margin: 0 10px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.searchButton:hover {
  background-color: #3e8ce3;
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.1);
}

/* Add this style for the search-container */
  
  /* Update the search-bar style */
  .search-bar {
    justify-content: center;
    align-items: center;
    width: 70%; /* Increase the width to 80% */
    max-width: 1200px; /* Limit the maximum width to 1200px */
    height: 40px;
    padding-left: 8px;
    padding-top: 6px;
    padding-bottom: 6px;
    margin: 0 auto;
    margin-top: 2rem;
    margin-bottom: 2rem;
  }

.image-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 2rem;
}
  
.searched-image {
    max-width: 100%;
    max-height: 90vh;
    object-fit: contain;
    cursor: pointer;
    margin: 1rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    border-radius: 5px;
}
  
.searched-image:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.1);
}
