.image-detail-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    /* justify-content: flex-start; Change from 'center' to 'flex-start' */
    padding-left: 1%; /* Add a small left padding to move the floating window close to the left side margin */
}

.plate-title {
  /* position: absolute; */
  top: 20px;  /* Increased to make room for the image */
  left: 20px;  /* Increased to make room for the image */
  font-size: 16px;
  font-weight: bold;
  color: black;
  z-index: 1010;  /* Added to ensure the title is above the image */
}



.image-detail {
  position: relative;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 90%;
  max-height: 90%;
  overflow: auto;
}

.close-button {
    position: fixed;
    top: 0px;
    right: 0px;
    width: 60px;
    height: 40px;
    border: none;
    background-color: rgb(10, 187, 227);
    color: rgb(0, 0, 0);
    font-weight: bold;
    border-radius: 50%;
    cursor: pointer;
}

.plate-container {
  position: relative;
  display: inline-block;
  width: 1528px;  /* New image width */
  height: 1032px;  /* New image height */
}

.well-buttons {
  position: absolute;
  top: -20px;
  left: -20px;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(8, 1fr);
  gap: 0.5px;
}

.well-button {
  display: flex;
  justify-content: left;
  align-items: center;
  width: 100%;
  height: 100%;
  border: 1px solid transparent;
  background-color: transparent;
  font-size: 12px;
  line-height: 1;
  text-align: center;
  cursor: pointer;
}

.well-button:hover {
  border-color: transparent; /* Change this to make the border invisible on hover */
  background-color: transparent; /* Change this to make the background invisible on hover */
}
