.well-detail-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}


.well-detail {
  position: relative;
  background-color: white;
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 90%; /* Increased from 80% */
  max-height: 90%; /* Increased from 80% */
  overflow: auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
