.close-button {
  position: fixed;
  top: 50px;
  right: 100px;
  width: 40px;
  height: 40px;
  border: none;
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  font-weight: bold;
  border-radius: 50%;
  cursor: pointer;
}

.well-info {
  display: flex;
  gap: 20px;
}

.well-image {
  width: auto;  /* Changed from 240px */
  max-width: 80%; /* Added */
  max-height: 80%; /* Added */
  flex-shrink: 0;
}

.well-data {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
