.endpoints-columns {
  display: flex;
  flex-direction: row;
  gap: 50px; /* Adjust the gap between columns as needed */
}

.endpoints-column {
  display: flex;
  flex-direction: column;
  gap: 10px; /* Adjust the gap between items within a column as needed */
}


.well-data label {
  display: flex;
  justify-content: space-between;
  font-size: 2.5em;
  align-items: center;
  margin-bottom: 10px;
}

.well-data button {
  align-self: flex-end;
  margin-top: auto;
  margin-left: auto;
  padding: 10px;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.well-data button:hover {
  background-color: #0056b3;
}

input[type="text"],
input[type="number"] {
  width: 50%; /* Adjust as needed */
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1.1em;
}
